<template>
  <tr class="nftrow">
    <td class="nftdata"><a :href="'https://opensea.io/collection/' + this.id" target="_blank">{{ this.id }}</a></td>
    <td class="nftdata"><a :href="'https://opensea.io/collection/' + this.id" target="_blank">{{ this.name }}</a></td>
    <td class="nftdata">{{ this.floor_price }} ETH </td>
    <td class="nftdata">{{ this.total_supply }}</td>
    <td class="nftdata">{{ this.no_of_owners }}</td>
    <td class="nftdata">{{ this.total_volume }}</td>
    <td class="nftdata">{{ this.one_day_sales }}</td>
    <td class="nftdata">{{ this.seven_day_sales }}</td>
    <td class="nftdata">{{ this.thirty_day_sales }}</td>
    <td class="nftdata">{{ this.timestamp }}</td>
  </tr>
</template>

<script>

export default {
  name: 'NFTRow',
  props: {
    slugId: String
  },
  data() {
    return {
      timer: "",
      timestamp: "",
      id: '',
      name: '',
      payment_token: '',
      floor_price: 0,
      total_supply: 0,
      no_of_owners: 0,
      total_volume: 0,
      one_day_sales: 0,
      seven_day_sales: 0,
      thirty_day_sales: 0,
    }
  },
  mounted() {
    this.getNFT(this.slugId);
    this.timer = setInterval(
      async() => {this.getNFT(this.slugId)},
      60000); 
  },
  methods : {
    async getNFT(slugId) {
      console.log("REFRESH")
      let url = 'https://api.opensea.io/collection/';
      url = url.concat(slugId)
      url = url.concat('?format=json')
      console.log(url)
      let prefix = process.env.NODE_ENV === 'development'
        ? 'https://enigmatic-chamber-26047.herokuapp.com/'
        : 'https://enigmatic-chamber-26047.herokuapp.com/'
      
      url = prefix.concat(url)
      const headers = new Headers();
      if (process.env.NODE_ENV === 'development') {
        headers.append(
          "Access-Control-Allow-Origin",
          "http://localhost:8080"
        );
      }
      else {
        headers.append(
          "Access-Control-Allow-Origin",
          "https://experiments.pocket-pasteboards.com"
        );
      }
      
      const request = new Request(
        url,
        {
          method: "GET",
          headers,
          cache: "default"
        }
      );

      const res = await fetch(request);
      const { collection } = await res.json();
      console.log(collection)
      this.id = collection.slug;
      this.name = collection.name;
      this.payment_token = collection.payment_tokens[0].symbol;
      this.floor_price = Math.round(collection.stats.floor_price * 1000)/1000;
      this.total_supply = Math.round(collection.stats.total_supply * 1000)/1000;
      this.no_of_owners = Math.round(collection.stats.num_owners * 1000)/1000;
      this.total_volume = Math.round(collection.stats.total_volume * 1000)/1000;
      this.one_day_sales = Math.round(collection.stats.one_day_sales * 1000)/1000;
      this.seven_day_sales = Math.round(collection.stats.seven_day_sales * 1000)/1000;
      this.thirty_day_sales = Math.round(collection.stats.thirty_day_sales * 1000)/1000;
      this.getCurrentTime();
    },
    getCurrentTime() {
      const today = new Date();
      this.timestamp = today.toLocaleTimeString();

    }
  }
}
</script>

<style scoped>
.nftrow {
  border: 1px solid black;
}
.nftdata {
  border: 1px solid black;
  text-align: center;
  padding: 4px 15px;
}
</style>